import React from 'react';
import style from "./Footer.module.css"
import styleContainer from "../common/styles/Container.module.css"

const Footer = () => {
    return <div className={style.footerBlock}>
        <div className={`${styleContainer.container} ${style.contactsContainer}`}>
            <h2 className={style.title}>Bohdan Korol</h2>
            <div className={style.socialMediaBlock}>
                <div className={style.icon}><img src={"https://www.freepngimg.com/thumb/facebook/62576-like-icons-button-computer-facebook-icon.png"}/></div>
                <div className={style.icon}><img src={"https://www.magnumandco.com.au/wp-content/uploads/2016/08/Instagram-logo-250x250.png"}/></div>
                <div className={style.icon}><img src={"https://i.ya-webdesign.com/images/official-linkedin-icon-png-4.png"}/></div>
                <div className={style.icon}><img src={"https://pluspng.com/img-png/telegram-logo-png-png-file-svg-980.png"}/></div>
            </div>
            <div><p>©2020 All rights reserved</p></div>
        </div>
    </div>
}

export default Footer